@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  all: unset;
  cursor: pointer; }

*,
input {
  box-sizing: border-box; }

input {
  border: none;
  box-sizing: border-box; }
  input:focus, input:active {
    outline: none; }

* {
  font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; }

html,
body {
  height: 100%; }

body {
  background-color: #f5f5f5;
  color: #444444;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px; }

main {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  min-height: 70vh; }

input[type="submit"],
button {
  cursor: pointer;
  width: 300px;
  padding: 10px;
  background-color: #ffd900;
  display: block;
  border-style: none;
  padding: 15px 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  font-size: 15px;
  text-align: center;
  box-shadow: 3px 2px #e7e7e7;
  background-color: rgba(255, 217, 0, 0.6); }
  input[type="submit"].small,
  button.small {
    width: 150px; }
  input[type="submit"]:hover,
  button:hover {
    background-color: rgba(255, 217, 0, 0.8); }

.button--delete {
  background-color: rgba(255, 0, 0, 0.6);
  color: white; }
  .button--delete:hover {
    background-color: rgba(255, 0, 0, 0.8); }

.header {
  width: 100%;
  top: 0px;
  left: 0px;
  background-color: #f5f5f5;
  vertical-align: center;
  margin-bottom: 30px; }
  .header .header__wrap {
    width: 1000px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr; }
    .header .header__wrap .header__column {
      vertical-align: center;
      line-height: 60px; }
      .header .header__wrap .header__column:nth-child(1) i {
        vertical-align: middle;
        font-size: 45px;
        color: #ea232c; }
      .header .header__wrap .header__column:nth-child(1) span {
        margin: 0px 5px;
        font-size: 25px;
        line-height: 45px;
        vertical-align: middle; }
      .header .header__wrap .header__column:nth-child(2) {
        padding: 10px; }
        .header .header__wrap .header__column:nth-child(2) form {
          width: 100%;
          margin: 0 auto; }
          .header .header__wrap .header__column:nth-child(2) form input {
            width: 100%;
            line-height: 60px;
            float: left;
            height: 40px;
            padding: 5px 20px;
            background-color: #e7e7e7;
            font-size: 20px;
            border-radius: 15px; }
          .header .header__wrap .header__column:nth-child(2) form input::placeholder {
            font-size: 15px;
            vertical-align: middle;
            font-weight: 300; }
      .header .header__wrap .header__column:last-child {
        text-align: right; }
        .header .header__wrap .header__column:last-child li {
          float: right;
          font-size: 15px;
          margin: 0px 10px;
          text-transform: uppercase; }

footer.footer {
  width: 100%;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.2);
  border-top-width: 3px;
  padding: 20px 0px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  footer.footer .wrap {
    width: 1000px;
    margin: 0 auto;
    text-align: center;
    color: rgba(0, 0, 0, 0.3); }
    footer.footer .wrap .footer__icon {
      font-size: 60px; }

.social-login {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .social-login button {
    display: block;
    border: none;
    padding: 15px 10px;
    margin: 0px 0px 10px 0px;
    width: 60%;
    border-radius: 10px;
    font-size: 18px; }
    .social-login button:nth-child(1) {
      background-color: rgba(36, 41, 46, 0.9);
      color: white; }
      .social-login button:nth-child(1):hover {
        background-color: #24292e; }
    .social-login button:last-child {
      background-color: rgba(66, 103, 178, 0.9);
      color: white; }
      .social-login button:last-child:hover {
        background-color: #4267b2; }
    .social-login button i {
      margin: 0px 5px; }

.video-detail__container {
  width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; }
  .video-detail__container .video__player {
    width: 100%;
    height: 50vmax;
    margin-bottom: 30px; }
    .video-detail__container .video__player video {
      height: 50vmax;
      width: 100%; }
  .video-detail__container .video__info {
    text-align: left;
    width: 1000px; }
    .video-detail__container .video__info .video__title {
      margin-top: 30px;
      margin-bottom: 20px;
      font-size: 35px;
      font-weight: 600; }
    .video-detail__container .video__info .video__views {
      font-size: 20px;
      color: rgba(0, 0, 0, 0.6);
      padding-bottom: 30px;
      margin-bottom: 20px;
      border-bottom: 1.5px solid rgba(0, 0, 0, 0.2); }
    .video-detail__container .video__info .video__description {
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 20px; }
  .video-detail__container .video__comment {
    font-size: 14px; }
    .video-detail__container .video__comment .video__comment-number {
      margin-top: 10px; }

.videoBlock video {
  margin-bottom: 10px;
  width: 100%;
  max-height: 100%; }

.videoBlock .videoBlock__title {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 10px; }

.form-container {
  width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .form-container form {
    align-items: center; }
    .form-container form .placeholder {
      font-size: 20px; }
    .form-container form input,
    .form-container form textarea {
      display: block;
      padding: 15px 10px;
      margin: 15px 0px;
      width: 500px;
      border-radius: 10px;
      box-shadow: 3px 2px #e7e7e7; }
      .form-container form input:last-child,
      .form-container form textarea:last-child {
        border: none;
        margin: 20px 20%;
        width: 60%;
        background-color: rgba(255, 217, 0, 0.6);
        font-size: 18px; }
        .form-container form input:last-child:hover,
        .form-container form textarea:last-child:hover {
          cursor: pointer;
          background-color: rgba(255, 217, 0, 0.8); }

.home-videos {
  display: grid;
  grid-template-columns: repeat(6, minmax(150px, 1fr));
  grid-gap: 30px; }
  .home-videos .videoBlock:first-child,
  .home-videos .videoBlock:nth-child(2) {
    width: 100%;
    grid-column: span 3; }
  .home-videos .videoBlock:nth-child(3),
  .home-videos .videoBlock:nth-child(4),
  .home-videos .videoBlock:nth-child(5) {
    grid-column: span 2; }

.form-container {
  display: flex; }
